<template>
  <main>
    <div class="main__header">
      <h1>CSS-Filters </h1>
    </div>
    <div class="main__container">
      <div class="main__working-area">
        <div class="main__grid-container">
          <p> </p>
          <div class="upload-image__container">
            <label
              for="myFile"
              class="upload-image__label"
            ><font-awesome-icon icon="fa-solid fa-cloud-arrow-down" /> </label>

            <input
              id="myFile"
              type="file"
              class="upload-image__input"
              name="myFile"
              multiple="multiple"
              @change="loadFile($event)"
            >
          </div>
          <div class="main__working-area__flex">
            <div class="working-area__cell">
              <figure
                id="myFigure"
                :style="{'filter': filterString}"
              >
                <img
                  id="myImage"
                  :src=" require('../assets/images/filter-image.jpg') "
                  alt="your image"
                >
              </figure>
            </div>
          </div>
        </div>
      </div>
      <div class="main__grid-settings-container">
        <div class="main__grid-settings">
          <h2></h2>
          <div class="flexbox-item__content">
            <div class="flexbox-item__div1">
              <div class="settings__grid-text">
                blur
                <div class="tooltip tooltip-settings">
                  <font-awesome-icon icon="fa-solid fa-clipboard-question" />
                  <span class="tooltiptext">.      .            ,    ,       .    ,   0.     CSS,     .</span>
                </div>
              </div>
            </div>

            <div class="flexbox-item__div2">
              <p class="control">
                <input
                  v-model="filterBlur"
                  type="number"
                  min="0"
                  max="10"
                  class="input__percent-value"
                >
                <span>px</span>
              </p>
            </div>

            <div class="flexbox-item__div3">
              <p class="control">
                <input
                  v-model="filterBlur"
                  type="range"
                  min="0"
                  max="10"
                  class="control__range-input"
                >
              </p>
            </div>
          </div>
          <div class="flexbox-item__content">
            <div class="flexbox-item__div1">
              <div class="settings__grid-text">
                brightness
                <div class="tooltip tooltip-settings">
                  <font-awesome-icon icon="fa-solid fa-clipboard-question" />
                  <span class="tooltiptext">.      ,      .  0%     .  100%    .      .  ,   100%,     .   ,   1.</span>
                </div>
              </div>
            </div>

            <div class="flexbox-item__div2">
              <p class="control">
                <input
                  v-model="filterBrightness"
                  type="number"
                  min="0"
                  max="200"
                  class="input__percent-value"
                >
                <span>%</span>
              </p>
            </div>

            <div class="flexbox-item__div3">
              <p class="control">
                <input
                  v-model="filterBrightness"
                  type="range"
                  min="0"
                  max="200"
                  class="control__range-input"
                >
              </p>
            </div>
          </div>
          <div class="flexbox-item__content">
            <div class="flexbox-item__div1">
              <div class="settings__grid-text">
                contrast
                <div class="tooltip tooltip-settings">
                  <font-awesome-icon icon="fa-solid fa-clipboard-question" />
                  <span class="tooltiptext">.   .  0%     .  100%    .     100%,      .   ,   1.</span>
                </div>
              </div>
            </div>

            <div class="flexbox-item__div2">
              <p class="control">
                <input
                  v-model="filterContrast"
                  type="number"
                  min="0"
                  max="200"
                >
                <span>%</span>
              </p>
            </div>

            <div class="flexbox-item__div3">
              <p class="control">
                <input
                  v-model="filterContrast"
                  type="range"
                  min="0"
                  max="200"
                  class="control__range-input"
                >
              </p>
            </div>
          </div>

          <div class="flexbox-item__content">
            <div class="flexbox-item__div1">
              <div class="settings__grid-text">
                grayscale
                <div class="tooltip tooltip-settings">
                  <font-awesome-icon icon="fa-solid fa-clipboard-question" />
                  <span class="tooltiptext"> .      .     .  100%    .  0%    .   0%  100%    .    ,   0.</span>
                </div>
              </div>
            </div>

            <div class="flexbox-item__div2">
              <p class="control">
                <input
                  v-model="filterGrayscale"
                  type="number"
                  min="0"
                  max="100"
                >
                <span>%</span>
              </p>
            </div>

            <div class="flexbox-item__div3">
              <p class="control">
                <input
                  v-model="filterGrayscale"
                  type="range"
                  min="0"
                  max="100"
                  class="control__range-input"
                >
              </p>
            </div>
          </div>
          <div class="flexbox-item__content">
            <div class="flexbox-item__div1">
              <div class="settings__grid-text">
                hue-rotate
                <div class="tooltip tooltip-settings">
                  <font-awesome-icon icon="fa-solid fa-clipboard-question" />
                  <span class="tooltiptext">  .     .       ,      .  0deg    .   ,   0deg.     ,      360deg .</span>
                </div>
              </div>
            </div>

            <div class="flexbox-item__div2">
              <p class="control">
                <input
                  v-model="filterHueRotate"
                  type="number"
                  min="0"
                  max="360"
                >
                <span>deg</span>
              </p>
            </div>

            <div class="flexbox-item__div3">
              <p class="control">
                <input
                  v-model="filterHueRotate"
                  type="range"
                  min="0"
                  max="360"
                  class="control__range-input"
                >
              </p>
            </div>
          </div>
          <div class="flexbox-item__content">
            <div class="flexbox-item__div1">
              <div class="settings__grid-text">
                invert
                <div class="tooltip tooltip-settings">
                  <font-awesome-icon icon="fa-solid fa-clipboard-question" />
                  <span class="tooltiptext">.    .    .  100%  .  0%    .   0%  100%   .   ,   0.</span>
                </div>
              </div>
            </div>

            <div class="flexbox-item__div2">
              <p class="control">
                <input
                  v-model="filterInvert"
                  type="number"
                  min="0"
                  max="100"
                >
                <span>%</span>
              </p>
            </div>

            <div class="flexbox-item__div3">
              <p class="control">
                <input
                  v-model="filterInvert"
                  type="range"
                  min="0"
                  max="100"
                  class="control__range-input"
                >
              </p>
            </div>
          </div>
          <div class="flexbox-item__content">
            <div class="flexbox-item__div1">
              <div class="settings__grid-text">
                opacity
                <div class="tooltip tooltip-settings">
                  <font-awesome-icon icon="fa-solid fa-clipboard-question" />
                  <span class="tooltiptext">.    .  0%    .  100%    .   0% 100%    .   ,   1.       opacity ;   ,           .</span>
                </div>
              </div>
            </div>

            <div class="flexbox-item__div2">
              <p class="control">
                <input
                  v-model="filterOpacity"
                  type="number"
                  min="0"
                  max="100"
                >
                <span>%</span>
              </p>
            </div>

            <div class="flexbox-item__div3">
              <p class="control">
                <input
                  v-model="filterOpacity"
                  type="range"
                  step="0.005"
                  min="0"
                  max="100"
                  class="control__range-input"
                >
              </p>
            </div>
          </div>
          <div class="flexbox-item__content">
            <div class="flexbox-item__div1">
              <div class="settings__grid-text">
                saturate
                <div class="tooltip tooltip-settings">
                  <font-awesome-icon icon="fa-solid fa-clipboard-question" />
                  <span class="tooltiptext">.     .    .  0%    .  100%    .      .     100%,    .   ,   1.</span>
                </div>
              </div>
            </div>

            <div class="flexbox-item__div2">
              <p class="control">
                <input
                  v-model="filterSaturate"
                  type="number"
                  min="0"
                  max="200"
                >
                <span>%</span>
              </p>
            </div>

            <div class="flexbox-item__div3">
              <p class="control">
                <input
                  v-model="filterSaturate"
                  type="range"
                  min="0"
                  max="200"
                  class="control__range-input"
                >
              </p>
            </div>
          </div>
          <div class="flexbox-item__content">
            <div class="flexbox-item__div1">
              <div class="settings__grid-text">
                sepia
                <div class="tooltip tooltip-settings">
                  <font-awesome-icon icon="fa-solid fa-clipboard-question" />
                  <span class="tooltiptext">.     .    .  0%    .  100%    .      .     100%,    .   ,   1.</span>
                </div>
              </div>
            </div>

            <div class="flexbox-item__div2">
              <p class="control">
                <input
                  v-model="filterSepia"
                  type="number"
                  min="0"
                  max="100"
                >
                <span>%</span>
              </p>
            </div>

            <div class="flexbox-item__div3">
              <p class="control">
                <input
                  v-model="filterSepia"
                  type="range"
                  min="0"
                  max="100"
                  class="control__range-input"
                >
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="main__code-fields">
      <base-code-field :unique-id="'html-code-selector'">
        <template #header>
          HTML-
        </template>
        <template #code>
          <pre><code><span class="element">&lt;figure</span> id="fdt-filter"<span class="element">&gt;</span>
  <span class="element">&lt;img</span><span> src=""</span><span class="element">&gt;</span>
<span class="element">&lt;figure/&gt;</span></code></pre>
        </template>
      </base-code-field>

      <base-code-field :unique-id="'css-code-selector'">
        <template #header>
          CSS-
        </template>
        <template #code>
          <pre><code><span class="element">#fdt-filter {</span>
  <span class="textArea">filter: {{ filterString }};</span>
<span class="element">}</span>
                      </code>
                  </pre>
        </template>
      </base-code-field>
    </div>
  </main>
</template>

<script>
export default {
  name: "VFilter",
  data() {
    return {
      filterBlur: "0",
      filterBrightness: "100",
      filterContrast: "100",
      filterGrayscale: "0",
      filterHueRotate: "0",
      filterInvert: "0",
      filterOpacity: "100",
      filterSaturate: "100",
      filterSepia: "0",
    }
  },
  computed: {
      filterString() {
        return `blur(${this.filterBlur}px)
        brightness(${this.filterBrightness}%)
        contrast(${this.filterContrast}%)
        grayscale(${this.filterGrayscale}%)
        hue-rotate(${this.filterHueRotate}deg)
        invert(${this.filterInvert}%)
        opacity(${this.filterOpacity}%)
        saturate(${this.filterSaturate}%)
        sepia(${this.filterSepia}%)`
      }
  },
  methods: {
    loadFile(event) {
      let image = document.getElementById('myImage');
      image.src = URL.createObjectURL(event.target.files[0]);
      this.filterBlur = "0";
      this.filterBrightness = "100";
      this.filterContrast = "100";
      this.filterGrayscale = "0";
      this.filterHueRotate = "0";
      this.filterInvert = "0";
      this.filterOpacity = "100";
      this.filterSaturate = "100";
      this.filterSepia = "0";
    },
  }
}
</script>

<style lang="scss" scoped>
.upload-image__container {
  display: inline-block;
  position: absolute;
  right: 25px;
  top: 15px;
  i {
    position: relative;
  }
}

.working-area__cell {
  box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.4);
  border: 1px solid #000;
  max-width: 45vw;
  max-height: 35vw;
  overflow: hidden;
}

.flexbox-item__content {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 30px);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  margin: 15px 5px;
}

.flexbox-item__content__select {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 30px;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  margin: 15px 5px;
}

.flexbox-item__div1 {
  grid-area: 1 / 1 / 2 / 3;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-left: 5px;
}

.input__percent-value::after {
  content: "%";
  background-color: #000;
  font-size: 0.8vw;
}

.upload-image__container {
  display: inline-block;
  position: absolute;
  right: 15px;
  top: 15px;
}

.upload-image__label {
  width: 15vw;
  min-height: 2vw;
  border-radius: 4px;
  text-align: center;
  cursor: pointer;
  font-size: 1vw;
  line-height: 2vw;
  transition: 1s;
  display: flex;
  border: 2px solid #000;
  align-items: center;
  svg {
    margin-left: 5px;
    margin-right: 5px;
    height: 25px;
  }
}

.upload-image__label:hover {
  color: $mainBlueColor;
  background-color: #fff;
}

.upload-image__input {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}
</style>